<template>
  <div id="app">
    <main>
      <img id = 'img1' v-if="show1" @click="showResult" src="./assets/homepage.png">
      <img id = 'img2' v-if="show2" src="./assets/result22.png">
      <div id = 'info' v-if="show2">
        疫苗接种完成
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      show1: true,
      show2: false,
      day: 0,
      resultDate: '2021-01-15'
    }
  },
  methods: {
    showResult () {
      this.show1 = false
      this.show2 = true
      const resultDate = new Date(this.resultDate)
      const now = Date.now()
      const deltaTime = now - resultDate.getTime()
      const deltaDay = Math.floor(deltaTime / 1000 / 3600 / 24)
      this.day = deltaDay
    }
  },
  mounted: () => {
    console.log(this.day)
  }
}
</script>

<style>
body {
  margin: 0;
  width: 100%;
  padding: 0;
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

main {
  text-align: center;
  width: 100%
}

main img{
  width: 100%
}

#info, #time, #day {
  position: absolute;
  top: 100px;
}

@keyframes fade {
    from {
        opacity: 1.0;
    }
    50% {
        opacity: 0.0;
    }
    to {
        opacity: 1.0;
    }
}

#info {
  top: 32.5rem;
  left: 0px;
  right: 0px;
  font-size: 2.6rem;
  font-weight: 900;
  color: #01ac61;
  animation: fade 1000ms infinite;
}

#day {
  padding: 0;
  margin: 0;
  width: 2.5rem;
  height: 2rem;
  text-align: center;
  line-height: 2.5rem;
  top: 36.3rem;
  left: 14.2rem;
  font-size: 1rem;
  color: #01ac61;
}

#time {
  top: 42.3rem;
  right: 2.9rem;
  font-size: 1rem;
}

header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}
</style>



